import { cookies } from "next/headers";
import { prisma } from "@/lib/prisma";

const SESSION_COOKIE = "studai_session";

export type CurrentUser =
  | {
      id: string;
      email: string;
      role?: string;
      name?: string | null;
    }
  | null;

/**
 * ✅ Resiliente: se o DB estiver fora, não derruba o app.
 * Retorna null e segue a vida.
 */
export async function getCurrentUser(): Promise<CurrentUser> {
  try {
    const cookieStore = await cookies();
    const token = cookieStore.get(SESSION_COOKIE)?.value;
    if (!token) return null;

    const session = await prisma.session.findUnique({
      where: { sessionToken: token },
      include: { user: true },
    });

    if (!session) return null;

    // Expirado
    if (session.expires && session.expires.getTime() < Date.now()) {
      return null;
    }

    if (!session.user) return null;

    return {
      id: session.user.id,
      email: session.user.email,
      role: (session.user as any).role,
      name: (session.user as any).name ?? null,
    };
  } catch (err) {
    // ✅ não quebra o app em dev quando DB estiver inacessível
    console.warn("[auth] DB indisponível, retornando user=null");
    return null;
  }
}
